import React, { useState } from 'react';
import { Palette, Check, X, RotateCcw } from 'lucide-react';

const RobotColorApp = () => {
  const [selectedPalette, setSelectedPalette] = useState('optimusPrime');
  const [selectedPart, setSelectedPart] = useState('head');
  const [colorInput, setColorInput] = useState('');
  const [feedback, setFeedback] = useState(null);
  
  // Current colors for each robot part
  const [colors, setColors] = useState({
    head: '#E0E0E0',
    body: '#90CAF9',
    leftArm: '#81C784',
    rightArm: '#81C784',
    leftLeg: '#FFB74D',
    rightLeg: '#FFB74D',
    antenna: '#F06292',
    eyes: '#FFFFFF'
  });

  // Define color palettes
  const palettes = {
    optimusPrime: {
      name: 'Optimus Prime',
      colors: ['#C41E3A', '#003DA5', '#FFD700', '#696969', '#000000'],
      icon: ''
    },
    wallE: {
      name: 'Wall-E',
      colors: ['#F4A460', '#D2691E', '#8B4513', '#FFD700', '#4169E1'],
      icon: ''
    },
    terminator: {
      name: 'The Terminator',
      colors: ['#2C2C2C', '#696969', '#C0C0C0', '#FF0000', '#1C1C1C'],
      icon: ''
    },
    ironGiant: {
      name: 'The Iron Giant',
      colors: ['#708090', '#4A4A4A', '#B0C4DE', '#2F4F4F', '#8B8B8B'],
      icon: ''
    },
    sophia: {
      name: 'Sophia',
      colors: ['#F5E6D3', '#D4AF37', '#FFFFFF', '#A9A9A9', '#FFE4B5'],
      icon: ''
    },
    cyborg: {
      name: 'Cyborg',
      colors: ['#1C1C1C', '#FF0000', '#00CED1', '#708090', '#4169E1'],
      icon: ''
    },
    asimo: {
      name: 'Asimo',
      colors: ['#FFFFFF', '#E0E0E0', '#C0C0C0', '#A9A9A9', '#000000'],
      icon: ''
    }
  };

  // Part labels
  const partNames = {
    head: 'Robot Head',
    body: 'Robot Body',
    leftArm: 'Left Arm',
    rightArm: 'Right Arm',
    leftLeg: 'Left Leg',
    rightLeg: 'Right Leg',
    antenna: 'Antenna',
    eyes: 'Eyes'
  };

  // Validate CSS color
  const isValidColor = (color) => {
    const s = new Option().style;
    s.color = color;
    return s.color !== '';
  };

  // Normalize color to hex
  const normalizeColor = (color) => {
    const s = new Option().style;
    s.color = color;
    const ctx = document.createElement('canvas').getContext('2d');
    ctx.fillStyle = color;
    return ctx.fillStyle.toUpperCase();
  };

  // Check if color matches palette
  const checkColor = () => {
    if (!colorInput.trim()) {
      setFeedback({ type: 'error', message: 'Please enter a color code!' });
      return;
    }

    if (!isValidColor(colorInput)) {
      setFeedback({ type: 'error', message: 'Invalid CSS color code. Try again!' });
      return;
    }

    const normalizedInput = normalizeColor(colorInput);
    const paletteColors = palettes[selectedPalette].colors.map(c => normalizeColor(c));

    if (paletteColors.includes(normalizedInput)) {
      setColors({ ...colors, [selectedPart]: colorInput });
      setFeedback({ 
        type: 'success', 
        message: `Perfect! That's from the ${palettes[selectedPalette].name} palette!` 
      });
    } else {
      setColors({ ...colors, [selectedPart]: colorInput });
      setFeedback({ 
        type: 'warning', 
        message: `Valid color, but not in the ${palettes[selectedPalette].name} palette. Try again!` 
      });
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      checkColor();
    }
  };

  const resetColors = () => {
    setColors({
      head: '#E0E0E0',
      body: '#90CAF9',
      leftArm: '#81C784',
      rightArm: '#81C784',
      leftLeg: '#FFB74D',
      rightLeg: '#FFB74D',
      antenna: '#F06292',
      eyes: '#FFFFFF'
    });
    setColorInput('');
    setFeedback(null);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-100 via-purple-100 to-pink-100 p-8">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="text-center mb-6">
          <h1 className="text-4xl font-bold mb-2" style={{ color: '#8B008B' }}>
            Color your robot using CSS - Workshop by USF Society of Women Engineers
          </h1>
          <p className="text-xl text-gray-600">
            Learn the code. Love the color.
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Left Panel - Robot Display */}
          <div className="bg-white rounded-lg shadow-xl p-8">
            <h2 className="text-2xl font-bold text-gray-800 mb-4 text-center">
              Your Robot
            </h2>
            <div className="bg-gradient-to-b from-gray-50 to-gray-100 rounded-lg border-2 border-gray-300 p-8 flex items-center justify-center min-h-[600px]">
              <svg 
                width="100%" 
                height="550px" 
                viewBox="0 0 300 400" 
                className="transition-all duration-300"
              >
                {/* Antenna */}
                <g 
                  className="cursor-pointer hover:opacity-80 transition-opacity"
                  onClick={() => setSelectedPart('antenna')}
                >
                  <line x1="150" y1="30" x2="150" y2="60" stroke={colors.antenna} strokeWidth="4" strokeLinecap="round"/>
                  <circle cx="150" cy="25" r="8" fill={colors.antenna} stroke="#333" strokeWidth="2"/>
                </g>

                {/* Head */}
                <g 
                  className="cursor-pointer hover:opacity-80 transition-opacity"
                  onClick={() => setSelectedPart('head')}
                >
                  <rect x="100" y="60" width="100" height="80" rx="15" fill={colors.head} stroke="#333" strokeWidth="3"/>
                  
                  {/* Eyes */}
                  <g 
                    className="cursor-pointer hover:opacity-80 transition-opacity"
                    onClick={(e) => { e.stopPropagation(); setSelectedPart('eyes'); }}
                  >
                    <circle cx="130" cy="95" r="12" fill={colors.eyes} stroke="#333" strokeWidth="2"/>
                    <circle cx="170" cy="95" r="12" fill={colors.eyes} stroke="#333" strokeWidth="2"/>
                    <circle cx="130" cy="95" r="6" fill="#333"/>
                    <circle cx="170" cy="95" r="6" fill="#333"/>
                  </g>
                  
                  {/* Mouth */}
                  <path d="M 120 115 Q 150 125 180 115" stroke="#333" strokeWidth="3" fill="none" strokeLinecap="round"/>
                </g>

                {/* Body */}
                <g 
                  className="cursor-pointer hover:opacity-80 transition-opacity"
                  onClick={() => setSelectedPart('body')}
                >
                  <rect x="90" y="150" width="120" height="140" rx="10" fill={colors.body} stroke="#333" strokeWidth="3"/>
                  
                  {/* Body details */}
                  <circle cx="150" cy="195" r="15" fill="none" stroke="#333" strokeWidth="2"/>
                  <rect x="120" y="230" width="60" height="8" rx="4" fill="#333" opacity="0.3"/>
                  <rect x="120" y="245" width="60" height="8" rx="4" fill="#333" opacity="0.3"/>
                  <rect x="120" y="260" width="60" height="8" rx="4" fill="#333" opacity="0.3"/>
                </g>

                {/* Left Arm */}
                <g 
                  className="cursor-pointer hover:opacity-80 transition-opacity"
                  onClick={() => setSelectedPart('leftArm')}
                >
                  <rect x="50" y="160" width="35" height="90" rx="8" fill={colors.leftArm} stroke="#333" strokeWidth="3"/>
                  <circle cx="67.5" cy="255" r="15" fill={colors.leftArm} stroke="#333" strokeWidth="3"/>
                </g>

                {/* Right Arm */}
                <g 
                  className="cursor-pointer hover:opacity-80 transition-opacity"
                  onClick={() => setSelectedPart('rightArm')}
                >
                  <rect x="215" y="160" width="35" height="90" rx="8" fill={colors.rightArm} stroke="#333" strokeWidth="3"/>
                  <circle cx="232.5" cy="255" r="15" fill={colors.rightArm} stroke="#333" strokeWidth="3"/>
                </g>

                {/* Left Leg */}
                <g 
                  className="cursor-pointer hover:opacity-80 transition-opacity"
                  onClick={() => setSelectedPart('leftLeg')}
                >
                  <rect x="105" y="295" width="35" height="70" rx="8" fill={colors.leftLeg} stroke="#333" strokeWidth="3"/>
                  <rect x="100" y="360" width="45" height="20" rx="10" fill={colors.leftLeg} stroke="#333" strokeWidth="3"/>
                </g>

                {/* Right Leg */}
                <g 
                  className="cursor-pointer hover:opacity-80 transition-opacity"
                  onClick={() => setSelectedPart('rightLeg')}
                >
                  <rect x="160" y="295" width="35" height="70" rx="8" fill={colors.rightLeg} stroke="#333" strokeWidth="3"/>
                  <rect x="155" y="360" width="45" height="20" rx="10" fill={colors.rightLeg} stroke="#333" strokeWidth="3"/>
                </g>
              </svg>
            </div>
            
            {/* Current Selection Indicator */}
            <div className="mt-6 text-center">
              <div className="text-sm text-gray-600 mb-2">Currently Coloring:</div>
              <div className="inline-flex items-center gap-3 bg-gradient-to-r from-blue-100 to-purple-100 px-6 py-3 rounded-lg border-2 border-blue-300 shadow-md">
                <div 
                  className="w-8 h-8 rounded-full border-2 border-gray-400 shadow-sm"
                  style={{ backgroundColor: colors[selectedPart] }}
                />
                <span className="font-bold text-gray-800 text-lg">{partNames[selectedPart]}</span>
              </div>
            </div>
          </div>

          {/* Right Panel - Controls */}
          <div className="space-y-6">
            {/* Palette Selection */}
            <div className="bg-white rounded-lg shadow-xl p-6">
              <h2 className="text-xl font-bold text-gray-800 mb-4 flex items-center gap-2">
                <Palette size={24} />
                Choose Color Palette
              </h2>
              <div className="grid grid-cols-2 gap-3">
                {Object.entries(palettes).map(([key, palette]) => (
                  <button
                    key={key}
                    onClick={() => {
                      setSelectedPalette(key);
                      setColorInput('');
                      setFeedback(null);
                    }}
                    className={`p-4 rounded-lg border-2 transition-all ${
                      selectedPalette === key
                        ? 'border-blue-500 bg-blue-50 shadow-md scale-105'
                        : 'border-gray-200 hover:border-gray-300 hover:shadow'
                    }`}
                  >
                    <div className="text-3xl mb-2">{palette.icon}</div>
                    <div className="font-semibold text-gray-800">{palette.name}</div>
                  </button>
                ))}
              </div>
            </div>

            {/* Palette Colors Reference */}
            <div className="bg-white rounded-lg shadow-xl p-6">
              <h3 className="text-lg font-bold text-gray-800 mb-3 flex items-center gap-2">
                {palettes[selectedPalette].icon} {palettes[selectedPalette].name} Colors
              </h3>
              <div className="grid grid-cols-5 gap-2">
                {palettes[selectedPalette].colors.map((color, idx) => (
                  <div key={idx} className="text-center">
                    <div
                      className="w-full h-16 rounded-lg border-2 border-gray-300 shadow-sm"
                      style={{ backgroundColor: color }}
                    />
                    <div className="text-xs text-gray-600 mt-1 font-mono">
                      {color}
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* Color Input */}
            <div className="bg-white rounded-lg shadow-xl p-6">
              <h3 className="text-lg font-bold text-gray-800 mb-3">
                Enter CSS Color Code:
              </h3>
              <div className="flex gap-2">
                <input
                  type="text"
                  value={colorInput}
                  onChange={(e) => setColorInput(e.target.value)}
                  onKeyPress={handleKeyPress}
                  placeholder="e.g., #90CAF9 or rgb(144,202,249)"
                  className="flex-1 px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-500 focus:outline-none font-mono text-lg"
                />
                <button
                  onClick={checkColor}
                  className="px-8 py-3 bg-gradient-to-r from-blue-500 to-purple-500 text-white rounded-lg hover:from-blue-600 hover:to-purple-600 transition-all font-bold shadow-md hover:shadow-lg"
                >
                  Apply
                </button>
              </div>
              <div className="mt-3 text-sm text-gray-500 bg-gray-50 p-3 rounded-lg">
                <strong>Supported format:</strong> CSS Color Code
              </div>
              <button
                onClick={resetColors}
                className="mt-3 w-full px-4 py-3 bg-gray-200 text-gray-700 rounded-lg hover:bg-gray-300 transition-colors flex items-center justify-center gap-2 font-semibold"
              >
                <RotateCcw size={18} />
                Reset All Colors to Default
              </button>
            </div>

            {/* Feedback */}
            {feedback && (
              <div
                className={`rounded-lg shadow-lg p-5 flex items-center gap-3 animate-fade-in ${
                  feedback.type === 'success'
                    ? 'bg-green-100 border-2 border-green-400'
                    : feedback.type === 'error'
                    ? 'bg-red-100 border-2 border-red-400'
                    : 'bg-yellow-100 border-2 border-yellow-400'
                }`}
              >
                {feedback.type === 'success' ? (
                  <Check className="text-green-600" size={28} />
                ) : (
                  <X className={feedback.type === 'error' ? 'text-red-600' : 'text-yellow-600'} size={28} />
                )}
                <span className="font-semibold text-gray-800 text-lg">{feedback.message}</span>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default RobotColorApp;